<template>
  <common-card class="debt-total" title="资产负债统计">
    <div class="content">
      <row
        row2
        title="总资产"
        count="1.233,144.03"
        :bar-width="filterData[0]"
        bar="+12%"
      />
      <row
        row2
        title="总负债"
        count="123.03"
        count-color="#DA302A"
        :bar-width="filterData[1]"
        bar="-12%"
      />
      <row
        row2
        title="净资产"
        count="1.233,144.03"
        :bar-width="filterData[2]"
        bar="+30%"
      />
    </div>
    <div class="debt-foot">
      <div class="item">
        <div>
          <div class="title">短期借款</div>
          <div class="count">1.233,144.03</div>
        </div>
        <div class="text-center">
          <img class="icon-detail" src="@/assets/detail.png" alt="" />
          <div class="detail">明细</div>
        </div>
      </div>
      <div class="item">
        <div>
          <div class="title">应付账款</div>
          <div class="count">1.233,144.03</div>
        </div>
        <div class="text-center">
          <img class="icon-detail" src="@/assets/detail.png" alt="" />
          <div>明细</div>
        </div>
      </div>
    </div>
  </common-card>
</template>

<script>
import CommonCard from "../CommonCard";
import Row from "../CommonRow";
export default {
  components: { CommonCard, Row },
  data() {
    return {
      data: [15, -15, 30],
    };
  },
  computed: {
    filterData() {
      // 把最大的用100%展示，其他的依照最大的算百分比
      let list = this.data.map((v) => Math.abs(v));
      let max = Math.max(...list);
      if (max > 100) {
        return list.map((v) => (v / max) * 100 + "%");
      } else {
        return list.map((v) => (v / 100) * 100 + "%");
      }
    },
  },
  methods: {},
};
</script>

<style lang="scss" scoped>
.debt-total {
  margin-top: 0;
  .content {
    margin-top: 39px;
    padding-left: 20px;
    padding-bottom: 39px;
    border-bottom: 1px solid #e8e8e8;
  }
  .debt-foot {
    padding: 28px 28px 47px 18px;
    .item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      & + .item {
        margin-top: 23px;
      }
      .title {
        font-size: 24px;
        color: #666666;
        margin-bottom: 26px;
      }
      .count {
        font-size: 42px;
        font-weight: bold;
        color: #333;
      }
      .icon-detail {
        width: 32px;
        height: 27px;
        margin-bottom: 22px;
      }
      .detail {
        font-size: 26px;
        color: #333;
      }
    }
  }
}
</style>
