<template>
<div class="profit-total">
  <common-card title="收入费用统计">
    <div class="date">
      <div class="left"></div>
      <span class="text">2021年12月</span>
      <div class="right"></div>
    </div>
    <div class="content">
      <row title="总利润" count='12,345,678.00' :bar-width='filterData[0]' bar='50%' />
      <row title="利润指标完成率" :bar-width='filterData[1]' bar='30%'>
        <template slot='count'>
          <vue-progress boss :value='30' />
        </template>
      </row>
      <div class="space"></div>
      <row title="息前利润" count='12,345,678.00' :bar-width='filterData[2]' bar='50%' />
      <row title="息后利润" count='12,345,678.00' :bar-width='filterData[3]' bar='30%' />
    </div>
  </common-card>
  <div class="bar-box">
    <div class="head">
      <span class="tit">营业收入</span>
      <span class="unit">(单位：万元)</span>
    </div>
    <div class="bar-echarts">
      <vue-echarts :options='options'></vue-echarts>
    </div>
  </div>
</div>
</template>

<script>
import CommonCard from "../CommonCard";
import Row from "../CommonRow";
export default {
  components: { CommonCard, Row},
  data() {
    return {
      data: [50, 30, 50, 30],
      options: {}
    };
  },
  computed: {
    filterData() {
      // 把最大的用100%展示，其他的依照最大的算百分比
      let list = this.data.map((v) => Math.abs(v));
      let max = Math.max(...list);
      if (max > 100) {
        return list.map((v) => (v / max) * 100 + "%");
      } else {
        return list.map((v) => (v / 100) * 100 + "%");
      }
    },
  },
  mounted() {
    let data = [334520, 122020, 122238]
    this.options = {
      tooltip: {},
      color: ['#FFCE7C', '#FFCE7C', '#DA302A'],
      grid: {
        top: 0,
        bottom: 0,
        left: 60
      },
      xAxis: {
        type: 'value',
        axisTick: {
          show: false
        },
        splitLine: {
          lineStyle: {
            color: '#E5E5E5'
          }
        }
      },
      yAxis: {
        type: 'category',
        data: ['2019年', '2020年', '2021年'],
        axisLabel: {
          align: 'right',
          overflow: 'truncate',
          width: 55,
          formatter: function(value, index) {
            console.log(value, index);
            return [
              `{a|${data[index]}}`,
              `{b|${value}}`
            ].join('\n')
          },
          rich: {
            a: {
              fontSize: 17,
              color: '#DA302A'
            },
            b: {
              fontSize: 10,
              color: '#666666',
              padding: [4, 0, 0, 0]
            }
          }
        },
        axisTick: {
          show: false
        },
        axisLine: {
          lineStyle: {
            color: '#E5E5E5'
          }
        }
      },
      series: [{
        type: 'bar',
        data,
        barWidth: '40%',
        colorBy: 'data',
        itemStyle: {
          borderRadius: [0, 14, 14, 0],
          label: {
            show: true
          }
        }
      }]
    }
  }
}
</script>

<style lang='scss' scoped>
.profit-total {
  .date {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 28px 0 34px;
    .text {
      font-size: 36px;
      font-weight: bold;
      margin: 0 40px;
    }
    .left,.right {
      width: 36px;
      height: 36px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: 12px 21px;
    }
    .left {
      background-image: url('~assets/left.png');
    }
    .right {
      background-image: url('~assets/right.png');
    }
  }
  .content {
    padding-left: 40px;
    padding-bottom: 40px;
    .space {
      margin-top: 100px;
    }
  }
  .bar-box {
    margin: 16px 34px;
    background-color: #fff;
    border-radius: 50px;
    padding: 56px 20px 45px 40px;
    .head {
      padding-right: 26px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 50px;
      .tit {
        font-size: 26px;
        font-weight: bold;
      }
      .unit {
        font-size: 20px;
      }
    }
    .bar-echarts {
      height: 265px;
    }
  }
}
</style>