<template>
  <common-card title="资产趋势对比">
    <template slot="head">
      <span class="unit">(单位：万元)</span>
    </template>
    <div class="tabs">
      <div
        class="tab-item"
        v-for="(item, index) in tabs"
        :key="index"
        :class="{ active: active === index }"
        @click="toggleTab(index)"
      >
        {{ item }}
      </div>
    </div>
    <div class="trend-pie">
      <vue-echarts :options="options" />
    </div>
  </common-card>
</template>

<script>
import CommonCard from "../CommonCard";
export default {
  components: { CommonCard },
  data() {
    return {
      tabs: ["总资产", "总负债", "净资产"],
      active: 1,
      options: {},
    };
  },
  mounted() {
    this.options = {
      grid: {
        top: 20,
        bottom: 30,
        left: 29,
        right: 29,
      },
      xAxis: {
        type: "category",
        boundaryGap: ['20%', '20%'],
        data: ["2019年", "2020年", "2021年"],
        axisLabel: {
          color: "#333333",
          fontSize: 12,
          margin: 10
        },
        axisTick: {
          show: false,
        },
        axisLine: {
          show: false,
        }
      },
      yAxis: {
        type: "value",
        axisLabel: {
          color: "#fff",
        },
        splitLine: {
          lineStyle: {
            color: "#EEEEEE",
          },
        },
        axisLine: {
          lineStyle: {
            color: "#EEEEEE",
          },
        },
        axisTick: {
          show: false,
        },
      },
      series: [
        {
          type: "line",
          data: [40, 28, 35],
          symbol: "circle",
          symbolSize: 5,
          label: {
            normal: {
              show: true,
              color: "#333333"
            },
          },
          itemStyle: {
            color: '#FA6669'
          },
          lineStyle: {
            opacity: 0
          },
          areaStyle: {
            color: {
              type: "linear",
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [
                {
                  offset: 0,
                  color: "#F84252"
                },
                {
                  offset: 0.52,
                  color: "#FF9871"
                },
                {
                  offset: 1,
                  color: "#ffffff"
                },
              ],
              global: false,
            },
          },
        },
      ],
    };
  },
  methods: {
    toggleTab(index) {
      this.active = index;
    },
  },
};
</script>

<style lang="scss" scoped>
.unit {
  font-size: 20px;
}
.tabs {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 46px 0 64px;
  .tab-item {
    font-size: 24px;
    color: #999999;
    padding: 0 25px;
    &.active {
      position: relative;
      color: #397efa;
      font-size: 32px;
      font-weight: bold;
      &::after {
        display: block;
        content: "";
        position: absolute;
        bottom: -18px;
        left: 0;
        right: 0;
        width: 59px;
        height: 7px;
        background: #cce2fe;
        border-radius: 4px;
        margin: auto;
      }
    }
  }
}
.trend-pie {
  width: 100%;
  height: 331px;
}
</style>
