<template>
  <div class="progress" :class="{ boss: boss }">
    <div
      v-for="item in 10"
      :key="item"
      class="item"
      :class="{ active: item <= index }"
    ></div>
    <span class="text">{{value}}%</span>
  </div>
</template>

<script>
export default {
  props: {
    boss: {
      type: Boolean,
      default: false,
    },
    value: {
      type: Number,
      required: true,
    },
  },
  computed: {
    index() {
      return Math.ceil(100 / this.value);
    },
  },
};
</script>

<style lang="scss" scoped>
.progress {
  display: flex;
  align-items: center;
  .text {
    font-size: 28px;
    margin-left: 17px;
  }
  .item {
    width: 9px;
    height: 19px;
    background-color: #fbf3e7;
    border-radius: 5px;
    & + .item {
      margin-left: 2px;
    }
    &.active {
      background-color: #ffd693;
    }
  }
  &.boss {
    .item {
      width: 15px;
      height: 31px;
      background-color: #ffdad9;
      border-radius: 8px;
      & + .item {
        margin-left: 4px;
      }
      &.active {
        background-color: #da302a;
      }
    }
  }
}
</style>
