<template>
  <div class="row" :class="{row2: row2}">
    <div class="left">
      <div class="title">
        {{ title }}
      </div>
      <slot name="count">
        <div class="count" :style="{ color: countColor }">
          {{ count }}
        </div>
      </slot>
    </div>
    <div class="right">
      <div class="title">
        {{ rightTitle }}
      </div>
      <slot name="right">
        <div class="bar-box">
          <div class="bar" :style="{ width: barWidth }">
            <div class="bfb c-red">{{ bar }}</div>
          </div>
        </div>
      </slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: String,
    rightTitle: { type: String, default: "同比" },
    count: String,
    countColor: { type: String, default: "#333" },
    bar: String,
    barWidth: String,
    row2: {type: Boolean, default: false}
  },
};
</script>

<style lang="scss" scoped>
.row {
  display: flex;
  & + .row {
    margin-top: 30px;
  }
  .left {
    width: 351px;
    flex-shrink: 0;
  }
  .right {
    flex: 1;
  }
  .title {
    font-size: 24px;
    color: #666666;
    margin-bottom: 20px;
  }
  .count {
    font-size: 34px;
    font-weight: bold;
    color: #333;
  }
  &.row2 {
    .title {
      font-size: 26px;
      margin-bottom: 26px;
    }
    .count {
      font-size: 46px;
    }
  }
  // 柱状图
  .bar-box {
    height: 46px;
    padding-right: 100px;
    .bar {
      position: relative;
      max-width: 100%;
      min-width: 25%;
      height: 33px;
      border-radius: 0px 17px 17px 0px;
      background-color: #da302a;
      &.debt {
        background-color: #ffc360;
      }
      // 百分比
      .bfb {
        position: absolute;
        top: 0;
        left: calc(100% + 10px);
        line-height: 33px;
        font-size: 32px;
        font-weight: bold;
        color: #333;
        &.c-red {
          color: #da302a;
        }
      }
    }
  }
}
</style>
